import React, { useEffect, useState, CSSProperties  } from "react";
import {
  ComponentProps,
  Streamlit,
  withStreamlitConnection,
} from "streamlit-component-lib";
import { Card, CardContent, CardActions, IconButton, Typography  } from '@material-ui/core';
import DeleteIcon from '@mui/icons-material/Delete';
import { Helmet } from 'react-helmet';
import { TextField, Button } from '@material-ui/core';
import { ClassTwoTone } from "@mui/icons-material";

interface Comment {
  id: number;
  user: string;
  date: string;
  text: string;
}


interface CommentAPI {
  user: string;
  comment_field_key: string;
  comment: string;
}


interface PythonStyle {
  border?: string;
  border_radius?: string;
  padding?: string;
  margin?: string;
}

interface ApiComment {
  id: number;
  user: string;
  date: string;
  comment: string;
  comment_field_key: string;
  deleted: number;
}


const CommentComponent = (props: ComponentProps) => {
  const [comments, setComments] = useState<Comment[]>(props.args.comments || []);
  const [newComment, setNewComment] = useState<string>("");


  const fetchComments = async () => {
    if (props.args.retrieve_from_api) {
      let apiUrl = `${host_url_api}/notes`;
      const queryParams = new URLSearchParams();
  
      if (auth_token) {
        queryParams.append('auth_token', auth_token);
      }
  
      if (props.args.comment_field_key) {
        queryParams.append('comment_field_key', props.args.comment_field_key);
      }
  
      const queryString = queryParams.toString();
      if (queryString) {
        apiUrl += `?${queryString}`;
      }
  
      try {
        const response = await fetch(apiUrl);
        if (!response.ok) {
          console.error('Failed to fetch comments:', response.statusText);
          return;
        }
        const apiCommentsData: ApiComment[] = await response.json();

    const transformedComments = apiCommentsData.map((apiComment: ApiComment) => ({
      id: apiComment.id,
      user: apiComment.user,
      date: apiComment.date,
      text: apiComment.comment,
    }));

    setComments(transformedComments);
      
      } catch (error) {
        console.error('Failed to fetch comments:', error);
      }
    } else {
      setComments(props.args.comments || []);
    }
  };
  useEffect(() => {
    fetchComments();
  }, [props.args.retrieve_from_api, props.args.comments]); // Re-fetch when retrieve_from_api or comments change
  // Function to handle new comment submission
  const handleCommentSubmit = async () => { // Mark this function as async
    const commentToAdd: Comment = {
      id: Date.now(), // or some other unique ID logic
      user: api_user_name, // Replace with actual user data if available
      date: new Date().toISOString(),
      text: newComment,
    };


  // send data to API
  if (return_mode === "api") {

    const commentToAddAPI: CommentAPI = {
      user: api_user_name, // Replace with actual user data if available
      comment_field_key: comment_field_key,
      comment: newComment,
    };


    let apiUrl = `${host_url_api}/notes`;
    if (auth_token) {
      apiUrl += `?auth_token=${encodeURIComponent(auth_token)}`;
    }
    

    try {
      const response = await fetch(apiUrl, { // 'await' can be used here
        method: 'POST',
        // mode: 'no-cors', // Add this line

        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(commentToAddAPI),

      });

      const responseData = await response.json(); // Await the JSON response
      commentToAdd.id = responseData.id || Date.now();
    
      if (!response.ok) {
        // Handle response errors
        console.error('Failed to post comment:', response.statusText);
        return;
      }
    } catch (error) {
      // Handle fetch errors
      console.error('Failed to post comment:', error);
      //create an error message and siaply it for 10 seocnds

      return;
    } 
  }

    let updatedComments;
    if (new_on_top) {
      updatedComments = [commentToAdd, ...comments]; // Add new comment to the top
    } else {
      updatedComments = [...comments, commentToAdd]; // Add new comment to the bottom
    }
    setComments(updatedComments);
    setNewComment(""); // Reset the input field

    // Send data back to Python, if needed
    // Streamlit.setComponentValue(updatedComments);
  };

  // Define textareaStyles with CSSProperties type
  const textareaStyles: CSSProperties = {
    height: 'calc(1.5em * 1.5 + 16px + 2px)', // Example calculation for 3 lines
    overflowY: 'auto', // Correctly typed for TypeScript
    margin: '2px'
  };

  const customCss = props.args.custom_css || "";
  const maxHeight  = props.args.max_height;
  const minHeight  = props.args.min_height;
  const delete_keyword = props.args.delete_keyword;
  const custom_font_awesome_url = props.args.custom_font_awesome_url;
  const delete_user = props.args.delete_user;
  const return_mode = props.args.return_mode;

  const allow_html_comment = props.args.allow_html_comment || false;
  const retrieve_from_api = props.args.retrieve_from_api || false;

  const auth_token = props.args.auth_token;
  const host_url_api = props.args.host_url_api;
  const textfield = props.args.textfield;
  const api_user_name = props.args.api_user_name || "Anonymous";
  const max_chars = props.args.max_chars;
  const button_post_label = props.args.button_post_label || "Post";
  const text_field_placeholder = props.args.text_field_placeholder || "Write a comment";
  const submitButtonColor = props.args.submit_button_color || "primary";
  const isCommentValid = newComment.trim().length > 0 && newComment.length <= max_chars;
  const new_on_top = props.args.new_on_top || false;

  const remainingChars = max_chars - newComment.length;
  const isOverLimit = remainingChars < 0;
  const comment_field_key = props.args.comment_field_key ;
  const no_comments_text = props.args.no_comments_text || "No comments yet";
  
  const handleDelete = async (id: number)   => { // Mark this function as async
    const deletedComment = comments.find((comment: Comment) => comment.id === id);
    const newComments = comments.filter((comment: Comment) => comment.id !== id);
    

    if (return_mode === "api") {
      let apiUrl = `${host_url_api}/notes/${id}?comment_field_key=${encodeURIComponent(comment_field_key)}`;
      if (auth_token) {
        apiUrl += `&auth_token=${encodeURIComponent(auth_token)}`;
      }
  
      try {
        const response = await fetch(apiUrl, {
          method: 'DELETE',
          headers: {
            'Content-Type': 'application/json',
          },
        }); 
  
        if (!response.ok) {
          console.error('Failed to delete comment:', response.statusText);
          return;
        }
    } catch (error) {
      // Handle fetch errors
      console.error('Failed to post comment:', error);
      //create an error message and siaply it for 10 seocnds

      return;
    }
  }
    
  setComments(newComments);
    if (return_mode === "all") {
      Streamlit.setComponentValue(newComments);
    } else if (return_mode === "deleted" && deletedComment) {
      Streamlit.setComponentValue([deletedComment]);
    }
  };
  useEffect(() => {
    Streamlit.setFrameHeight();
  }, [comments]);

  return (
    <>
      <Helmet>
        <script
          src={custom_font_awesome_url}
          crossOrigin="anonymous"
          id="font-awesome-icons"
        />
      </Helmet>
      <div style={{ display: 'flex', flexDirection: 'column', height: '100%' }}>
        <div style={{ flexGrow: 1, overflowY: 'auto', maxHeight: maxHeight, minHeight: minHeight, padding: '5px' }}>
            {
      comments.length === 0 ? (
        // Display this when there are no comments
        <div style={{ textAlign: 'center', padding: '20px' }}>
          <Typography>  <span dangerouslySetInnerHTML={{ __html: no_comments_text }}></span> </Typography>
   
        </div>
      ) : (
          comments.map((comment: Comment) => (
            <Card key={comment.id} style={{ position: 'relative' }}>
              <CardContent>
                <Typography variant="body2" color="textSecondary" component="p">
                  {comment.user} ({new Date(comment.date).toISOString().split('T')[0]})
                </Typography>
                <Typography variant="body1" component="p">
                  {allow_html_comment ? (
                    <span dangerouslySetInnerHTML={{ __html: comment.text }}></span>
                  ) : (
                    <span>{comment.text}</span>
                  )}
                </Typography>
              </CardContent>
              {(delete_user === "all" || delete_user === comment.user) ? (
                <CardActions style={{ paddingTop: 0, position: 'absolute', right: '1px', top: '1px', padding: "2px" }}>
                  <IconButton aria-label="delete" color="secondary" size="small" onClick={() => handleDelete(comment.id)}>
                    <DeleteIcon />
                    <span dangerouslySetInnerHTML={{ __html: delete_keyword }}></span>
                  </IconButton>
                </CardActions>
              ) : (
                <CardActions style={{ visibility: 'hidden', paddingTop: 0, position: 'absolute', right: '1px', top: '1px', padding: "2px" }}>
                  <IconButton aria-label="delete" color="secondary" size="small">
                    <DeleteIcon style={{ visibility: 'hidden' }} />
                  </IconButton>
                </CardActions>
              )}
            </Card>
          ))
          )}

        </div>
        {props.args.textfield && (
          <div style={{ display: 'flex', flexDirection: 'column', gap: '10px', padding: '5px' }}>
            <div style={{ position: 'relative' }}>
            <TextField
              label= {text_field_placeholder}
              variant="outlined"
              fullWidth
              value={newComment}
              onChange={(e) => setNewComment(e.target.value)}
              multiline
              maxRows={2}
              className="CommentField"
              id="commentfield"
              inputProps={{ maxLength: max_chars }}
              InputProps={{
                style: {  overflowY: 'auto' },
                inputProps: { style: textareaStyles }
              }}
            />
             <span style={{ 
                position: 'absolute', 
                bottom: 5, 
                right: 20, 
                fontSize: '0.75rem', 
                color: remainingChars < 0 ? 'red' : 'grey' 
              }}>
                {`${newComment.length}/${max_chars}`}
              </span>
            </div>
            <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
              <Button
                color="primary"
                variant="contained"
                onClick={handleCommentSubmit}
                disabled={!isCommentValid}
                fullWidth
                style={{ backgroundColor: submitButtonColor ? submitButtonColor : undefined }}
              >
                {button_post_label}
              </Button>
            </div>
          </div>
        )}
      </div>
    </>
  );
  
}
export default withStreamlitConnection(CommentComponent);
